// Ð¡Ð¾Ð·Ð´Ð°ÐµÐ¼ ÐºÐ»Ð°ÑÑ Ð¿Ð¾Ð¿Ð°Ð¿Ð°
export default class Popup {
  // Ð² ÐºÐ¾Ð½ÑÑÑÑÐºÑÐ¾Ñ Ð¿ÑÐ¸Ð½Ð¸Ð¼Ð°ÐµÐ¼ ÑÐµÐ»ÐµÐºÑÐ¾Ñ Ð¿Ð¾Ð¿Ð°Ð¿Ð° ÑÑÑÐ°Ð½Ð¸ÑÑ
	constructor(popupSelector) {
    // ÑÐµÐ»ÐµÐºÑÐ¾Ñ Ð¿Ð¾Ð¿Ð°Ð¿Ð°
		this._popupSelector = document.querySelector(popupSelector);
    this._handleEscClose = this._handleEscClose.bind(this);
    this._popup = document.querySelector(popupSelector);
	}

  // Ð¼ÐµÑÐ¾Ð´ Ð·ÑÐºÑÑÑÐ¸Ñ Ð¿Ð¾Ð¿Ð°Ð¿Ð° Ð½Ð° Esc
  _handleEscClose(evt) {
    if(evt.key === "Escape") {
      this.close();
    }
  }

  // Ð¼ÐµÑÐ¾Ð´ Ð´Ð»Ñ Ð¾ÑÐºÑÑÑÐ¸Ñ Ð¿Ð¾Ð¿Ð°Ð¿Ð°
  open() {
    this._popupSelector.classList.add('popup_opened');
    document.addEventListener('keydown', this._handleEscClose);
  }

  // Ð¼ÐµÑÐ¾Ð´ Ð´Ð»Ñ Ð·Ð°ÐºÑÑÑÐ¸Ñ Ð¿Ð¾Ð¿Ð°Ð¿Ð°
  close() {
    this._popupSelector.classList.remove('popup_opened');
    document.removeEventListener('keydown', this._handleEscClose);
  }

  // ÑÐ»ÑÑÐ°ÑÐµÐ»Ð¸
  setEventListeners() {
    // Ð·Ð°ÐºÑÑÑÐ¸Ðµ popup Ð½Ð° ÐºÑÐµÑÑÐ¸Ðº
    this._popupSelector.querySelector('.popup__button-close').addEventListener('click', () => {
      this.close();
    });
    // Ð·Ð°ÐºÑÑÑÐ¸Ðµ popup Ð½Ð° overlay
    this._popup.addEventListener('click', (evt) => {
      if (evt.target === this._popup) {
        this.close();
      }
    });
  }
}