// ------------------- ÑÐ¾Ð·Ð´Ð°Ð½Ð¸Ðµ ÑÐºÐ·ÐµÐ¼Ð¿Ð»ÑÑÐ° ÐºÐ»Ð°ÑÑÐ° ÐºÐ°ÑÑÐ¾ÑÐºÐ¸ -------------------
const createCard = (cardData) => {
  const card = new Card({
    // Ð¾Ð±ÑÐµÐºÑ Ñ Ð´Ð°Ð½Ð½ÑÐ¼Ð¸ ÐºÐ°ÑÑÐ¾ÑÐºÐ¸
    data: {
      name: cardData.name,
      link: cardData.link,
      cardID: cardData._id,
      owner: cardData.owner,
      likes: cardData.likes
    },
    // Ð¿ÑÐ¾Ð¸Ð·Ð¾Ð¹Ð´ÐµÑ Ð¿ÑÐ¸ ÐºÐ»Ð¸ÐºÐµ Ð½Ð° ÐºÐ°ÑÑÐ¸Ð½ÐºÑ
    handleCardClick: (name, link) => {
      popupZoomImage.open(name, link);
    },
    // Ð¿ÑÐ¾Ð¸Ð·Ð¾Ð¹Ð´ÐµÑ Ð¿ÑÐ¸ ÐºÐ»Ð¸ÐºÐµ Ð½Ð° Ð»Ð°Ð¹Ðº
    handleLikeClick: (cardInfo, likes, user, likeButton, likesCounter) => {
      let likesOnCard = likes;
      if(likesOnCard.includes(user._id)) {
        api.deleteLike(cardInfo.cardID)
        .then(res => {
          likeButton.classList.remove('element__like_active');
          likesOnCard.splice(likesOnCard.indexOf(user._id),1);
          likesCounter.textContent = res.likes.length;
        })
        .catch(error => {
          console.log(error);
        })
      } else {
        api.setLike(cardInfo.cardID)
        .then(res => {
          likeButton.classList.add('element__like_active');
          likesOnCard.push(user._id);
          likesCounter.textContent = res.likes.length;
        })
        .catch(error => {
          console.log(error);
        })
      }
    },
    // Ð¿ÑÐ¾Ð¸Ð·Ð¾Ð¹Ð´ÐµÑ Ð¿ÑÐ¸ ÐºÐ»Ð¸ÐºÐµ Ð½Ð° ÑÐ´Ð°Ð»ÐµÐ½Ð¸Ðµ
    handleDeleteIconClick: (cardID) => {
      popupCardDeleteConfirm.open();
      popupCardDeleteConfirm.createHandleSubmit(() => {
        api.deleteCard(cardID)
        .then(res => {
          console.log(res);
          card.removeCardLayout();
          popupCardDeleteConfirm.close();
        })
        .catch(error => {
          console.log(error);
        })
      });
    }
    },
    '#card-template',
    configGenerationCards,
    userInfo.getUser()
  );
  const cardElement = card.generateCard();
  return cardElement;
};
