import Popup from './Popup.js';

export default class PopupWithForm extends Popup {
  constructor(popupSelector, handleFormSubmit) {
    super(popupSelector);
    // ÐºÐ¾Ð»Ð±ÑÐº ÑÐ°Ð±Ð¼Ð¸ÑÐ° ÑÐ¾ÑÐ¼Ñ
    this._handleFormSubmit = handleFormSubmit;
    this._popup = document.querySelector(popupSelector);
    this._form = this._popup.querySelector('.form');
  }

  // Ð¼ÐµÑÐ¾Ð´ Ð´Ð»Ñ Ð·Ð°ÐºÑÑÑÐ¸Ñ Ð¿Ð¾Ð¿Ð°Ð¿Ð°
  close() {
    super.close(); // Ð²ÑÐ·ÑÐ²Ð°ÐµÐ¼ ÑÐ¾Ð´Ð¸ÑÐµÐ»ÑÑÐºÐ¸Ð¹ Ð¼ÐµÑÐ¾Ð´
    this._form.reset();
  }

  // ÑÐ»ÑÑÐ°ÑÐµÐ»Ñ ÐºÐ»Ð¸ÐºÐ°
  setEventListeners() {
    super.setEventListeners(); // Ð²ÑÐ·ÑÐ²Ð°ÐµÐ¼ ÑÐ¾Ð´Ð¸ÑÐµÐ»ÑÑÐºÐ¸Ð¹ Ð¼ÐµÑÐ¾Ð´
    // Ð¾Ð±ÑÐ°Ð±Ð¾ÑÑÐ¸Ðº ÑÐ°Ð±Ð¼Ð¸ÑÐ°
    this._form.addEventListener('submit', () => {
      this._handleFormSubmit(this._getInputValues());
    });
  }

  // ÑÐ¾Ð±Ð¸ÑÐ°ÐµÐ¼ Ð´Ð°Ð½Ð½ÑÐµ Ð²ÑÐµÑ Ð¿Ð¾Ð»ÐµÐ¹ ÑÐ¾ÑÐ¼Ñ
  _getInputValues() {
    const inputArray = Array.from(this._form.querySelectorAll('.form__input'));

    const inputsValue = inputArray.map((item) => {
      return item.value;
    });

    return inputsValue;
  }
}