// Ð¡Ð¾Ð·Ð´Ð°ÐµÐ¼ ÐºÐ»Ð°ÑÑ ÐºÐ°ÑÑÐ¾ÑÐºÐ¸ Ð¸ Ð¿ÑÐ¸Ð¼ÐµÐ½ÑÐµÐ¼ ÑÐºÑÐ¿Ð¾ÑÑ Ð¿Ð¾-ÑÐ¼Ð¾Ð»ÑÐ°Ð½Ð¸Ñ
export default class FormValidator {
  // Ð² ÐºÐ¾Ð½ÑÑÑÑÐºÑÐ¾Ñ Ð¿ÑÐ¸Ð½Ð¸Ð¼Ð°ÐµÐ¼ Ð¾Ð±ÑÐµÐºÑ Ñ ÑÐµÐ»ÐµÐºÑÐ¾ÑÐ°Ð¼Ð¸ ÑÐ¾ÑÐ¼Ñ Ð¸ ÑÐµÐºÑÑÐ°Ñ ÑÐ¾ÑÐ¼Ð°
	constructor(data, formElement) {
    // ÑÐµÐ»ÐµÐºÑÐ¾ÑÑ ÑÐ¾ÑÐ¼Ñ
		this._formSelector = data.formSelector;
		this._inputSelector = data.inputSelector;
	  this._submitButtonSelector = data.submitButtonSelector;
    this._inactiveButtonClass = data.inactiveButtonClass;
    this._inputErrorClass = data.inputErrorClass;
    this._errorClass = data.errorClass;
    this._fieldsSelector = data.fieldsSelector;
    // ÑÐµÐºÑÑÐ°Ñ ÑÐ¾ÑÐ¼Ð°
    this._formElement = formElement;

    this._buttonElement = this._formElement.querySelector(this._submitButtonSelector);
    this._inputList = Array.from(this._formElement.querySelectorAll(this._inputSelector));
    this._fieldsetList = Array.from(this._formElement.querySelectorAll(this._fieldsSelector));
	}

  // ÑÑÐ½ÐºÑÐ¸Ñ Ð¾ÑÐ¾Ð±ÑÐ¶ÐµÐ½Ð¸Ñ Ð¾ÑÐ¸Ð±ÐºÐ¸ (Ð¿Ð¾Ð»Ðµ Ð²Ð²Ð¾Ð´Ð°, ÑÐ¾Ð¾Ð±ÑÐµÐ½Ð¸Ðµ Ð¾ÑÐ¸Ð±ÐºÐ¸)
  _showInputError = (inputElement, errorMessage) => {
    // Ð½Ð°ÑÐ¾Ð´Ð¸Ð¼ Ñ Ð´Ð°Ð½Ð½Ð¾Ð¹ ÑÐ¾ÑÐ¼Ñ ÑÐµÐ»ÐµÐºÑÐ¾Ñ ÑÐ¾Ð¾Ð±ÑÐµÐ½Ð¸Ñ Ð¾ÑÐ¸Ð±ÐºÐ¸, Ð¿ÑÐ¸Ð½Ð°Ð´Ð»ÐµÐ¶Ð°ÑÐ¸Ð¹ Ð¿Ð¾Ð»Ñ Ð²Ð²Ð¾Ð´Ð°
    // Ð¸ Ð·Ð°Ð½Ð¾ÑÐ¸Ð¼ Ð´Ð°Ð½Ð½ÑÐµ Ð² ÐºÐ¾Ð½ÑÑÐ°Ð½ÑÑ
    const errorElement = this._formElement.querySelector(`.${inputElement.id}-error`);
    // Ð´Ð¾Ð±Ð°Ð²Ð»ÑÐµÐ¼ Ð¿Ð¾Ð»Ñ Ð²Ð²Ð¾Ð´Ð° ÑÐµÐ»ÐµÐºÑÐ¾Ñ Ð¾ÑÐ¸Ð±ÐºÐ¸ (ÐºÑÐ°ÑÐ½Ð°Ñ Ð³ÑÐ°Ð½Ð¸ÑÐ°)
    inputElement.classList.add(this._inputErrorClass);
    // Ð·Ð°Ð½Ð¾ÑÐ¸Ð¼ Ð² ÑÐµÑÑ Ð¾ÑÐ¸Ð±ÐºÐ¸ Ð²ÑÐ¾Ð´Ð½Ð¾Ðµ ÑÐ¾Ð¾Ð±ÑÐµÐ½Ð¸Ðµ Ð¾ÑÐ¸Ð±ÐºÐ¸
    errorElement.textContent = errorMessage;
    // Ð´ÐµÐ»Ð°ÐµÐ¼ ÑÐµÐºÑÑ Ð¾ÑÐ¸Ð±ÐºÐ¸ Ð²Ð¸Ð´Ð¸Ð¼ÑÐ¼
    errorElement.classList.add(this._errorClass);
  };

  // ÑÑÐ½ÐºÑÐ¸Ñ ÑÐºÑÑÑÐ¸Ñ Ð¾ÑÐ¸Ð±ÐºÐ¸ (Ð¿Ð¾Ð»Ðµ Ð²Ð²Ð¾Ð´Ð°)
  _hideInputError = (inputElement) => {
    // Ð½Ð°ÑÐ¾Ð´Ð¸Ð¼ Ñ Ð´Ð°Ð½Ð½Ð¾Ð¹ ÑÐ¾ÑÐ¼Ñ ÑÐµÐ»ÐµÐºÑÐ¾Ñ ÑÐ¾Ð¾Ð±ÑÐµÐ½Ð¸Ñ Ð¾ÑÐ¸Ð±ÐºÐ¸, Ð¿ÑÐ¸Ð½Ð°Ð´Ð»ÐµÐ¶Ð°ÑÐ¸Ð¹ Ð¿Ð¾Ð»Ñ Ð²Ð²Ð¾Ð´Ð°
    // Ð¸ Ð·Ð°Ð½Ð¾ÑÐ¸Ð¼ Ð´Ð°Ð½Ð½ÑÐµ Ð² ÐºÐ¾Ð½ÑÑÐ°Ð½ÑÑ
    const errorElement = this._formElement.querySelector(`.${inputElement.id}-error`);
    // ÑÐ´Ð°Ð»ÑÐµÐ¼ ÑÐµÐ»ÐµÐºÑÐ¾Ñ Ð¾ÑÐ¸Ð±ÐºÐ¸ Ð¿Ð¾Ð»Ñ Ð²Ð²Ð¾Ð´Ð° (ÐºÑÐ°ÑÐ½Ð°Ñ Ð³ÑÐ°Ð½Ð¸ÑÐ°)
    inputElement.classList.remove(this._inputErrorClass);
    // ÑÐºÑÑÐ²Ð°ÐµÐ¼ ÑÐµÐºÑÑ Ð¾ÑÐ¸Ð±ÐºÐ¸
    errorElement.classList.remove(this._errorClass);
    // Ð¾ÑÐ¸ÑÐ°ÐµÐ¼ ÑÐµÐºÑÑ Ð¾ÑÐ¸Ð±ÐºÐ¸
    errorElement.textContent = '';
  };

  // ÑÑÐ½ÐºÑÐ¸Ñ Ð¿ÑÐ¾Ð²ÐµÑÐºÐ¸ Ð¿Ð¾Ð»Ñ Ð½Ð° Ð²Ð°Ð»Ð¸Ð´Ð½Ð¾ÑÑÑ (Ð¿Ð¾Ð»Ðµ Ð²Ð²Ð¾Ð´Ð°)
  _checkInputValidity = (inputElement) => {
    // ÐµÑÐ»Ð¸ Ð¿Ð¾Ð»Ðµ Ð½Ðµ Ð¿ÑÐ¾ÑÐ¾Ð´Ð¸Ñ Ð²Ð°Ð»Ð¸Ð´Ð°ÑÐ¸Ñ - Ð¿Ð¾ÐºÐ°Ð¶ÐµÐ¼ Ð¾ÑÐ¸Ð±ÐºÐ¸
    if (!inputElement.validity.valid) {
      this._showInputError(inputElement, inputElement.validationMessage);
      // Ð¸Ð½Ð°ÑÐµ - ÑÐºÑÐ¾ÐµÐ¼ ÐµÐµ
    } else {
      this._hideInputError(inputElement);
    }
  };

  // ÑÑÐ½ÐºÑÐ¸Ñ Ð´Ð¾Ð±Ð°Ð²Ð»ÐµÐ½Ð¸Ñ ÑÐ»ÑÑÐ°ÑÐµÐ»ÐµÐ¹ Ðº Ð¿Ð¾Ð»ÑÐ¼ (ÑÐ¾ÑÐ¼Ð°)
  _setEventListeners = () => {
    // Ð²ÑÐ·ÑÐ²Ð°ÐµÐ¼ ÑÑÐ½ÐºÑÐ¸Ñ Ð¿ÐµÑÐµÐºÐ»ÑÑÐµÐ½Ð¸Ñ ÑÐ¾ÑÑÐ¾ÑÐ½Ð¸Ñ ÐºÐ½Ð¾Ð¿ÐºÐ¸ sabmit
    this._toggleButtonState();
    // Ð¿ÑÐ¾ÑÐ¾Ð´Ð¸Ð¼ÑÑ Ð¿Ð¾ Ð¼Ð°ÑÑÐ¸Ð²Ñ Ð¸ Ðº ÐºÐ°Ð¶Ð´Ð¾Ð¼Ñ Ð¿Ð¾Ð»Ñ Ð²Ð²Ð¾Ð´Ð° Ð¿ÑÐ¸Ð¼ÐµÐ½ÑÐµÐ¼ ÑÑÐ½ÐºÑÐ¸Ñ
    this._inputList.forEach((inputElement) => {
      // Ð´Ð¾Ð±Ð°Ð²Ð»ÑÐµÐ¼ ÑÐ»ÑÑÐ°ÑÐµÐ»Ñ ÑÐ¾Ð±ÑÑÐ¸Ñ Ð½Ð° Ð²Ð²Ð¾Ð´ Ð² Ð¿Ð¾Ð»Ðµ
      inputElement.addEventListener('input', () => {
        // Ð²ÑÐ·ÑÐ²Ð°ÐµÐ¼ ÑÑÐ½ÐºÑÐ¸Ñ Ð¿ÑÐ¾Ð²ÐµÑÐºÐ¸ Ð²Ð°Ð»Ð¸Ð´Ð½Ð¾ÑÑÐ¸ Ð¿Ð¾Ð»Ñ
        this._checkInputValidity(inputElement);
        // Ð¸ ÑÑÐ½ÐºÑÐ¸Ñ Ð¿ÐµÑÐµÐºÐ»ÑÑÐµÐ½Ð¸Ñ ÑÐ¾ÑÑÐ¾ÑÐ½Ð¸Ñ ÐºÐ½Ð¾Ð¿ÐºÐ¸ sabmit
        this._toggleButtonState();
      });
    });
  };

  // ÑÑÐ½ÐºÑÐ¸Ñ, Ð¾ÑÐ²ÐµÑÑÑÐ²ÐµÐ½Ð½Ð°Ñ Ð·Ð° Ð²ÐºÐ»ÑÑÐµÐ½Ð¸Ðµ Ð²Ð°Ð»Ð¸Ð´Ð°ÑÐ¸Ð¸ ÑÐ¾ÑÐ¼
  enableValidation = () => {
    this._formElement.addEventListener('submit', (evt) => {
      // Ð·Ð°Ð¿ÑÐµÑÐ°ÐµÐ¼ Ð±ÑÐ°ÑÐ·ÐµÑÑ Ð¾Ð±Ð½Ð¾Ð²Ð»ÑÑÑ ÑÑÑÐ°Ð½Ð¸ÑÑ
      evt.preventDefault();
    });
    // Ðº ÐºÐ°Ð¶Ð´Ð¾Ð¼Ñ Ð¿Ð¾Ð»Ñ Ð¿ÑÐ¸Ð¼ÐµÐ½ÑÐµÐ¼ ÑÑÐ½ÐºÑÐ¸Ñ
    this._fieldsetList.forEach((fieldSet) => {
      // Ð²ÑÐ·ÑÐ²Ð°ÐµÐ¼ ÑÑÐ½ÐºÑÐ¸Ñ Ð´Ð¾Ð±Ð°Ð²Ð»ÐµÐ½Ð¸Ñ ÑÐ»ÑÑÐ°ÑÐµÐ»ÐµÐ¹
      this._setEventListeners(fieldSet);
    });
  };

  // ÑÑÐ½ÐºÑÐ¸Ñ Ð¿ÑÐ¾Ð²ÐµÑÐºÐ¸ Ð¿Ð¾Ð»Ñ Ð½Ð° Ð²Ð°Ð»Ð¸Ð´Ð½Ð¾ÑÑÑ
  _hasInvalidInput = () => {
    // Ð²Ð¾Ð·Ð²ÑÐ°ÑÐ°ÐµÐ¼ true, ÐµÑÐ»Ð¸ ÑÐ¾ÑÑ Ð±Ñ Ð¾Ð´Ð¸Ð½ Ð½Ðµ Ð¿ÑÐ¾ÑÐµÐ» Ð¿ÑÐ¾Ð²ÐµÑÐºÑ
    return this._inputList.some((inputElement) => {
      // Ð¿ÑÐ¾Ð²ÐµÑÑÐµÐ¼ ÑÑÐ»Ð¾Ð²Ð¸Ðµ, ÐµÑÑÑ Ð»Ð¸ ÑÐ¾ÑÑ Ð±Ñ Ð¾Ð´Ð¸Ð½ ÑÐ»ÐµÐ¼ÐµÐ½Ñ, Ð½Ðµ Ð¿ÑÐ¾ÑÐµÐ´ÑÐ¸Ð¹ Ð²Ð°Ð»Ð¸Ð´Ð°ÑÐ¸Ñ
      return !inputElement.validity.valid;
    });
  };

  // ÑÑÐ½ÐºÑÐ¸Ñ Ð¿ÐµÑÐµÐºÐ»ÑÑÐµÐ½Ð¸Ñ ÑÐ¾ÑÑÐ¾ÑÐ½Ð¸Ñ ÐºÐ¿Ð¾Ð½ÐºÐ¸ sabmit
  _toggleButtonState = () => {
    // ÐµÑÐ»Ð¸ ÑÐ¾ÑÑ Ð±Ñ Ð¾Ð´Ð½Ð¾ Ð¿Ð¾Ð»Ðµ Ð²Ð²Ð¾Ð´Ð° Ð½Ðµ Ð¿ÑÐ¾ÑÐ»Ð¾ Ð¿ÑÐ¾Ð²ÐµÑÐºÑ Ð½Ð° Ð²Ð°Ð»Ð¸Ð´Ð½Ð¾ÑÑÑ
    if (this._hasInvalidInput()) {
      // Ð´ÐµÐ»Ð°ÐµÐ¼ ÐºÐ½Ð¾Ð¿ÐºÑ Ð½ÐµÐ°ÐºÑÐ¸Ð²Ð½Ð¾Ð¹
      this._buttonElement.classList.add(this._inactiveButtonClass);
      this._buttonElement.disabled = true;
      // Ð¸Ð½Ð°ÑÐµ - Ð´ÐµÐ»Ð°ÐµÐ¼ Ð°ÐºÑÐ¸Ð²Ð½Ð¾Ð¹
    } else {
      this._buttonElement.classList.remove(this._inactiveButtonClass);
      this._buttonElement.disabled = false;
    }
  };

  // ÑÑÐ½ÐºÑÐ¸Ñ Ð¾Ð±Ð½ÑÐ»ÐµÐ½Ð¸Ñ Ð¾ÑÐ¸Ð±Ð¾Ðº
  resetFormErrors = () => {
    // Ð±Ð»Ð¾ÐºÐ¸ÑÑÐµÐ¼ ÐºÐ½Ð¾Ð¿ÐºÑ Ð¿Ð¾ ÑÐ¼Ð¾Ð»ÑÐ°Ð½Ð¸Ñ
    this._toggleButtonState();
    // Ð¿ÑÐ¾ÑÐ¾Ð´Ð¸Ð¼ Ð¿Ð¾ ÐºÐ°Ð¶Ð´Ð¾Ð¼Ñ Ð¿Ð¾Ð»Ñ Ð²Ð²Ð¾Ð´Ð°
    this._inputList.forEach((inputArrayElement) => {
      // Ð½Ð°ÑÐ¾Ð´Ð¸Ð¼ ÑÐ¾Ð¾ÑÐ²ÐµÑÑÑÐ²ÑÑÑÐ¸Ð¹ span Ð¾ÑÐ¸Ð±ÐºÐ¸
      const errorElement = this._formElement.querySelector(`.${inputArrayElement.id}-error`);
      // Ð¾ÑÐ¸ÑÐ°ÐµÐ¼ Ð¾ÑÐ¸Ð±ÐºÐ¸
      inputArrayElement.classList.remove(this._inputErrorClass);
      errorElement.classList.remove(this._errorClass);
      errorElement.textContent = '';
    });
  };
}
