// Ð¡Ð¾Ð·Ð´Ð°ÐµÐ¼ ÐºÐ»Ð°ÑÑ ÐºÐ°ÑÑÐ¾ÑÐºÐ¸ Ð¸ Ð¿ÑÐ¸Ð¼ÐµÐ½ÑÐµÐ¼ ÑÐºÑÐ¿Ð¾ÑÑ Ð¿Ð¾-ÑÐ¼Ð¾Ð»ÑÐ°Ð½Ð¸Ñ
export default class Card {
  // Ð² ÐºÐ¾Ð½ÑÑÑÑÐºÑÐ¾Ñ Ð¿ÑÐ¸Ð½Ð¸Ð¼Ð°ÐµÐ¼ Ð¾Ð±ÑÐµÐºÑ Ñ ÑÐ¾Ð´ÐµÑÐ¶Ð¸Ð¼ÑÐ¼ ÐºÐ°ÑÑÐ¾ÑÐµÐº,
  // Ð¾Ð±ÑÐµÐºÑ Ñ ÑÐµÐ»ÐµÐºÑÐ¾ÑÐ°Ð¼Ð¸ ÑÐ°Ð±Ð»Ð¾Ð½Ð°, ÑÐµÐ»ÐµÐºÑÐ¾Ñ template Ð¸ ÑÑÐ½ÐºÑÐ¸Ñ Ð¾ÑÐºÑÑÑÐ¸Ñ Ð¿Ð¾Ð¿Ð°Ð¿Ð° Ñ ÐºÐ°ÑÑÐ¸Ð½ÐºÐ¾Ð¹
	constructor(
      {
        data,
        handleCardClick,
        handleLikeClick,
        handleDeleteIconClick
      },
      cardSelector,
      config,
      user) {
    // Ð´Ð°Ð½Ð½ÑÐµ Ð¿Ð¾Ð»ÑÐ·Ð¾Ð²Ð°ÑÐµÐ»Ñ
    this._user = user;
    // callback-Ð¸
    this._handleCardClick = handleCardClick;
    this._handleLikeClick = handleLikeClick;
    this._handleDeleteIconClick = handleDeleteIconClick;
    // ÐºÐ°ÑÑÐ¾ÑÐºÐ°
		this._cardName = data.name;
		this._cardLink = data.link;
    this._cardId = data.cardID;
    this._cardOwner = data.owner;
    this._cardLikes = data.likes;
    this._cardInfo = data;
    // ÑÐ°Ð±Ð»Ð¾Ð½
    this._cardElement = config.cardElement;
    this._cardImage = config.cardImage;
    this._cardTitle = config.cardTitle;
    this._cardLike = config.cardLike;
    this._cardTrash = config.cardTrash;
    this._cardLikeCounter = config.cardLikeCounter;
    // ÑÐµÐ»ÐµÐºÑÐ¾Ñ ÑÐ°Ð±Ð»Ð¾Ð½Ð°
		this._cardSelector = cardSelector;
	}

  // Ð¿ÑÐ¸Ð²Ð°ÑÐ½ÑÐ¹ Ð¼ÐµÑÐ¾Ð´, Ð¿Ð¾Ð¼ÐµÑÐ°ÐµÐ¼ Ð² Ð¿ÐµÑÐµÐ¼ÐµÐ½Ð½ÑÑ ÐºÐ»Ð¾Ð½ ÑÐ°Ð±Ð»Ð¾Ð½Ð° Ð¸ Ð²Ð¾Ð·Ð²ÑÐ°ÑÐ°ÐµÐ¼ ÐµÐµ
	_getTemplate() {
    const cardElement = document.querySelector(this._cardSelector)
    .content.querySelector(this._cardElement).cloneNode(true);
    return cardElement;
  }

  // Ð¿ÑÐ±Ð»Ð¸ÑÐ½ÑÐ¹ Ð¼ÐµÑÐ¾Ð´, ÑÐ¾Ð·Ð´Ð°ÐµÑ ÐºÐ°ÑÑÐ¾ÑÐºÑ Ð¸ Ð½Ð°Ð²ÐµÑÐ¸Ð²Ð°ÐµÑ Ð¾Ð±ÑÐ°Ð±Ð¾ÑÑÐ¸ÐºÐ¸
  generateCard() {
    this._element = this._getTemplate();
    this._cardPicture = this._element.querySelector(this._cardImage);
    const deleteButton = this._element.querySelector(this._cardTrash);
    const likeButton = this._element.querySelector(this._cardLike);
    this._likeCounter = this._element.querySelector(this._cardLikeCounter);
    //  Ð½Ð°Ð¿Ð¾Ð»Ð½ÑÐµÐ¼ ÐºÐ°ÑÑÐ¾ÑÐºÑ ÑÐ¾Ð´ÐµÑÐ¶Ð¸Ð¼ÑÐ¼ Ð¸Ð· Ð¾Ð±ÑÐµÐºÑÐ°
    this._cardPicture.src = this._cardLink;
    this._cardPicture.alt = this._cardName;
    this._element.querySelector(this._cardTitle).textContent = this._cardName;
    this._likeCounter.textContent = this._cardLikes.length;

    // ÑÐ¾Ð±Ð¸ÑÐ°ÐµÐ¼ Ð¼Ð°ÑÑÐ¸Ð² id-Ð¿Ð¾Ð»ÑÐ·Ð¾Ð²Ð°ÑÐµÐ»ÐµÐ¹, Ð¿Ð¾ÑÑÐ°Ð²Ð¸Ð²ÑÐ¸Ñ Ð»Ð°Ð¹Ðº ÐºÐ°ÑÑÐ¾ÑÐºÐµ
    this._likes = Array.from(this._cardLikes).map(item => {
      return item._id;
    });

    // Ð´ÐµÐ»Ð°ÐµÐ¼ Ð¿ÑÐ¾Ð²ÐµÑÐºÑ Ð½Ð° ÑÐ¾Ð±ÑÑÐ²ÐµÐ½Ð½ÑÐ¹ Ð»Ð°Ð¹Ðº Ñ ÐºÐ°ÑÑÐ¾ÑÐºÐ¸ Ð¿ÑÐ¸ Ð¿ÐµÑÐ²Ð¾Ð½Ð°ÑÐ°Ð»ÑÐ½Ð¾Ð¹ Ð·Ð°Ð³ÑÑÐ·ÐºÐµ ÑÑÑÐ°Ð½Ð¸ÑÑ
    if(this._likes.includes(this._user._id)) {
      likeButton.classList.add('element__like_active');
    } else {
      likeButton.classList.remove('element__like_active');
    }

    // Ð½Ð°Ð²ÐµÑÐ¸Ð²Ð°ÐµÐ¼ Ð¾Ð±ÑÐ°Ð±Ð¾ÑÑÐ¸ÐºÐ¸
    // ÑÐ»ÑÑÐ°ÑÐµÐ»Ñ Ð»Ð°Ð¹ÐºÐ°
    likeButton.addEventListener('click', () => {
      this._handleLikeClick(this._cardInfo,
                            this._likes,
                            this._user,
                            likeButton,
                            this._likeCounter);
    });

    // ÑÐ»ÑÑÐ°ÑÐµÐ»Ñ ÑÐ´Ð°Ð»ÐµÐ½Ð¸Ñ
    if(this._cardOwner._id === this._user._id) {
      deleteButton.addEventListener('click', () => {
        this._handleDeleteIconClick(this._cardId);
      });
    } else {
      deleteButton.remove();
    }

    // ÑÐ»ÑÑÐ°ÑÐµÐ»Ñ Ð½Ð°Ð¶Ð°ÑÐ¸Ñ Ð½Ð° Ð¸Ð·Ð¾Ð±ÑÐ°Ð¶ÐµÐ½Ð¸Ðµ
    this._handleCardZoomListener();

    // Ð²Ð¾Ð·Ð²ÑÐ°ÑÐ°ÐµÐ¼ Ð³Ð¾ÑÐ¾Ð²ÑÑ ÐºÐ°ÑÑÐ¾ÑÐºÑ
    return this._element;
  }

  // Ð¼ÐµÑÐ¾Ð´ ÑÐ´Ð°Ð»ÐµÐ½Ð¸Ñ ÐºÐ°ÑÑÐ¾ÑÐºÐ¸ Ð¸Ð· ÑÐ°Ð·Ð¼ÐµÑÐºÐ¸
  removeCardLayout() {
    this._element.closest(this._cardElement).remove();
  }

  // Ð¿ÑÐ¸Ð²Ð°ÑÐ½ÑÐ¹ Ð¼ÐµÑÐ¾Ð´, ÑÐ»ÑÑÐ°ÑÐµÐ»Ñ Ð½Ð°Ð¶Ð°ÑÐ¸Ñ Ð½Ð° Ð¸Ð·Ð¾Ð±ÑÐ°Ð¶ÐµÐ½Ð¸Ðµ
  _handleCardZoomListener() {
    this._cardPicture.addEventListener('click', () => {
      this._handleCardClick(this._cardName, this._cardLink);
    });
  }
}
